/* montserrat-300 - latin_cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/montserrat-v25-latin_cyrillic-300.eot"); /* IE9 Compat Modes */
  src: local(""), url("../fonts/montserrat-v25-latin_cyrillic-300.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/montserrat-v25-latin_cyrillic-300.woff2") format("woff2"),
    /* Super Modern Browsers */ url("../fonts/montserrat-v25-latin_cyrillic-300.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/montserrat-v25-latin_cyrillic-300.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("../fonts/montserrat-v25-latin_cyrillic-300.svg#Montserrat") format("svg"); /* Legacy iOS */
}
